.menu_section {
	padding: 116px 0;
	display: flex;
	flex-direction: column;

}


.menu_container {
	display: flex;
	flex-direction: column;
	gap: 88px;
}


.menu_section_icon-dessert {
	width: 360px;
	height: 161px;
}

.menu_select_wrap {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
}

.select_wrap_title {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: var(--subtitle-1);
	color: var(--black-100);
}

.title_line_wrap {
	display: flex;
	flex-direction: column;
   align-items: center;
}

.select_wrap_svg {
	width: 121px;
	height: 3px;
}

.select_item {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: var(--subtitle);
	text-transform: uppercase;
	color: var(--black-100);
	position: relative;
}

.select_wrap_list {
	display: flex;
	align-items: center;
	gap: 12px;
	padding: 0;
}


.select_item:not(:last-child)::after {
	content: "";
	position: absolute;
	right: -9px;
	top: 50%;
	transform: translateY(-50%);
	width: 6px;
	height: 6px;
	background-color: var(--black-100);
	border-radius: 100%;
}

.menu_block_wrap {
	display: flex;
	flex-direction: column;
	gap: 72px;

	position: relative;
}

.menu_block_wrap::before {
	content: "";
	background-image: url("/src/images/menu/menu_line.svg");
	background-size: 360px 161px;
	background-position: right;
	background-repeat: no-repeat;
	position: absolute;
	top: -110px;
	left: 0;
	width: 360px;
	height: 161px;
	z-index: -1;
}

.menu_select_block {
	display: flex;
	flex-direction: column;
	align-items: center;

	position: relative;
	gap: 14px;
}

.select_block_title {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: var(--large-3);
	color: var(--black-100);
}

.select_block_text {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: var(--subtitle-2);
	color: var(--black-100);
}


.block_svg_first {
	width: 143px;
	height: 49px;
	z-index: -1;
	position: absolute;
	top: 20px;
	transform: translateY(-50%);
}

.block_svg_second {
	width: 129px;
	height: 22px;
	z-index: -1;
	position: absolute;
	top: 68px;
	transform: translateY(-50%);
}


.select_img_list {
	display: flex;
	flex-wrap: wrap;
	gap: 40px 46px;
	padding: 0;
	margin: 0;
	position: relative;
}

.select_img_item {
	flex: 1 1 calc(50% - 23px);
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 11px;
}

.select_img_item:last-child {
	margin-top: 8px;
}

.select_img_item img {
	/* max-width: 100%;
	max-height: 100%; */
	object-fit: contain;
	width: 95px;
	height: 201px;
}

.select_img_item:nth-child(2) img {
	width: 117px;
	height: 296px;
	z-index: 10;
	max-width: none;
	max-height: none;
	position: absolute;
	top: -92px;
	left: 280px;
	transform: translateX(-50%);
}

.img_description {
	position: absolute;
	top: 210px;

	display: flex;
	flex-direction: column;
	gap: 11px;
}

.select_img_text_wrap {
	display: flex;
	flex-direction: column;
	gap: 11px;
}

.select_chimney_title {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: var(--body-2);
	text-align: center;
	color: var(--black-100);
}

.select_chimney_text {
	font-family: var(--font-family);
	font-weight: var(--normal);
	font-size: 10px;
	text-align: center;
	color: var(--main-orange);
}


@media only screen and (min-width: 744px) {
.menu_section {
		padding-top: 160px;

	}

	.menu_container {
		gap: 100px;
	}


	.menu_section_icon-dessert {
