		height: 161px;
	}

	
	.menu_select_wrap {
		gap: 33px;
	}

	.select_wrap_title {
		font-size: var(--title-1);
	}

	.select_wrap_svg {
		width: 123px;
		height: 7px;
	}

		.select_item {
			font-size: var(--body-2);
		}
	
		.select_wrap_list {
			gap: 16px;
		}
	
		.select_item:not(:last-child)::after {
			content: "";
			position: absolute;
			top: 44%;
			right: -10px;
		}
	
		.menu_block_wrap {
			gap: 80px;
		}

		.menu_block_wrap::before {
		width: 690px;
		height: 316px;
		background-size: 690px 316px;
		top: -150px;
		left: 60px;
}
	
		.select_block_title {
			font-size: 56px;
		}
	
		.select_block_text {
			font-size: var(--subtitle-3);
		}
		.menu_select_block {
			gap: unset;
		}

		.block_svg_first {
			width: 191px;
			height: 58px;
			top: 26px;
		}
	
		.block_svg_second {
			width: 145px;
			height: 22px;
			top: 77px;
		}

		.select_img_list {
		gap: 52px 38px;
		width: 665px;
		height: 1196px;
		}
		
		.menu_text_wrapper {
		display: flex;
		flex-direction: column;
		gap: 18px;
		width: 100%;
		text-align: center;
		}
		
		.select_img_item {
		flex: 1 1 calc((100% - 2 * 52px) / 3);
		width: 100%;
		height: 338px;
		}
		
		.select_img_item img {
			width: 146px;
			height: 272px;
		}

		.select_img_item:nth-child(2) img {
		width: 139px;
		height: 354px;
		top: -82px;
		left: 50%;
		}
		
		.img_description {
		flex-direction: column;
		gap: 18px;
		display: flex;
		position: absolute;
		top: 284px;
		width: 100%;
		text-align: center;
				}
		
				.select_img_text_wrap {
					gap: 18px;
				}
		
				.select_chimney_title {
					font-size: 15px;

				}
		
				.select_chimney_text {
					font-size: var(--subtitle);
				}
		
}

