@import "https://fonts.cdnfonts.com/css/kharkiv-tone";
@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;1,500&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}





.header_wrap {
  background: var(--header-bg-color);
  width: 100%;
  background-size: cover;
  border-bottom: 1px solid #000;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  padding: 0 8px;
  display: flex;
}

.header-nav, .header-switch-wrap, .header-contacts {
  display: none;
}

.header-logo-link > svg {
  width: 80px;
  height: 80px;
  flex-shrink: 0;
}

.header-burger-opener {
  stroke-width: 1px;
  stroke: var(--black-100);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media only screen and (min-width: 744px) {
  .header_container {
    padding: 12px 40px;
  }

  .header-logo-link > svg {
    width: 70px;
    height: 72px;
    flex-shrink: 0;
  }

  .header-language-switcher {
    display: block;
  }

  .header-switch-wrap {
    height: 34px;
    align-items: center;
    display: flex;
  }

  .language-switcher-btn {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }

  .header-burger-opener {
    display: none;
  }

  .header-contacts {
    width: 144px;
    height: 24px;
    gap: 16px;
    display: flex;
  }

  .link {
    width: 1em;
    height: 1em;
    stroke-width: 1px;
    stroke: currentColor;
    fill: rgba(0, 0, 0, 0);
    display: inline-block;
  }

  .header_nav_cont_wrap {
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;
    display: flex;
  }

  .header-nav {
    gap: 30px;
    display: flex;
  }

  .header-nav-link {
    font-family: var(--font-family);
    font-weight: var(--normal);
    font-size: var(--body-2);
